import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class StockManagementService {

  constructor(private http: HttpClient) { }


  getStock(): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/all`;
    return this.http.get<any[]>(invItemUrl);
  }
  getStockById(params: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}//api/v1/inventory-items/find/inventory/by/id`, { params });
  }


  addStock(params: any, details: any): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/add`;
    return this.http.post<any>(invItemUrl, details, { params });
  }

  updateStock(CategoryDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/update`;
    return this.http.put<any>(updateBillUrl, CategoryDetails)
  }

  calculateTotalCost(): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/inventory-items/cost`);
  }


  deleteStockPemanently(params: any): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/delete`;
    return this.http.delete<any>(invItemUrl, { params });


  }

  checkStockAvailability(dataToValidate: any): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/check/availability/of/items`;
    return this.http.post<any>(invItemUrl, dataToValidate)
  }



  // ********************************************************************************************************************
  // Requistions

  //transfer items to branchstores
  transfer2BranchStore(params: any, details: any): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/main_stores/issueInventoryItemsToBranchStore`;
    return this.http.post<any>(invItemUrl, details, { params });
  }



  //********************************************************************************************************************** */
  getStocksFromProcurement(): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/register/items/all`;
    return this.http.get<any[]>(invItemUrl);
  }
  getStockFromProcurementById(params: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/inventory-items/register/find/by/id`, { params });
  }

  getStockFromProcurementByStatus(params: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/inventory-items/register/items/all/by/status`, { params });
  }



  addStockFromProcurement(params: any, details: any): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/add`;
    return this.http.post<any>(invItemUrl, details, { params });
  }

  updateStockFromProcurement(CategoryDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/register/update`;
    return this.http.put<any>(updateBillUrl, CategoryDetails)
  }


  deleteStockFromProcurementPemanently(params: any): Observable<any> {
    const invItemUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/delete`;
    return this.http.delete<any>(invItemUrl, { params });


  }

  validateStocksFromProcurement(dataToValidate: any): Observable<any> {
    const imprestUrl = `${environment.baseUrlProcurement}/api/v1/inventory-items/register/push/to/inventory`;
    return this.http.put<any>(imprestUrl, dataToValidate)
  }





}
