import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class MainstoreService {

  constructor(private http: HttpClient) { }

  getMainStore(): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/allStores`;
    return this.http.get<any[]>(mainStoreUrl);
  }

  addMainStore(data: any): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/add`;
    return this.http.post<any>(mainStoreUrl, data);
  }

  updateMainStore(rfpDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/main_stores/update`;
    return this.http.put<any>(updateBillUrl, rfpDetails)
  }

  getItemsInTransit(): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/allInTransitItems`;
    return this.http.get<any[]>(mainStoreUrl);
  }


  deleteMainStorePemanently(params: any): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/delete`;
    return this.http.delete<any>(mainStoreUrl, { params });
  }

  //*********************************************************************************************************** */
  // requistions from the branch 
  // http://localhost:9090/api/main_stores/fetchAllRequisitions
  fetchAllRequisitions(): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/fetchAllRequisitions`;
    return this.http.get<any[]>(mainStoreUrl)
  }

  //fetch all branch store requsitions

  // http://localhost:9090/api/main_stores/requestedGoods
  getAllBranchRequsitions(): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/requestedGoods`;
    return this.http.get<any[]>(mainStoreUrl)
  }

  //filter through branch requisitions by branch
  // http://localhost:9090/api/main_stores/fetchAllRequisitionsByBranchCode?branchCode=11111
  filterRequisitionsByBranch(params: any): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/fetchAllRequisitionsByBranchCode`;
    return this.http.get<any>(mainStoreUrl, { params });
  }

  //filter all requisitions by status 
  // http://localhost:9090/api/main_stores/allRequisitions?status=InProgress
  filterAllRequisitionsByStatus(params: any): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/allRequisitions`;
    return this.http.get(mainStoreUrl, { params });
  }

  //realize a requisition to the branch store
  // http://localhost:9090/api/main_stores/issueInventoryItemsToBranchStore?branchCode=11&requisitionCode=111
  issueInventoryItemsToBranchStore(requestBody, params: any):Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/issueInventoryItemsToBranchStore`;
    return this.http.post<any>(mainStoreUrl, requestBody, {params});
  }

  //*********************************************************************************************************** */
  // Statistics

  // http://52.15.152.26:9090/api/main_stores/delete?storeId=1
  genReport(id: any): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/MainStoreReports/${id}`;
    return this.http.post<any>(mainStoreUrl, id);
  }


  // http://localhost:9090/api/main_stores/inventory-items/total-stock-per-category
  getTotalStockPerCategory(): Observable<any> {
    const mainStoreUrl = `${environment.baseUrlProcurement}/api/main_stores/inventory-items/total-stock-per-category`;
    return this.http.get<any>(mainStoreUrl)
  }


}