import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class ItemCategoryService {

  constructor(private http: HttpClient) { }

  getItemCategories(): Observable<any> {
    const ItemCatUrl = `${environment.baseUrlProcurement}/api/v1/ItemCategory/all`;
    return this.http.get<any[]>(ItemCatUrl);
  }

  getItemCategoriesId(id: any): Observable<any> {
    return this.http.get<any>(`${environment.baseUrlProcurement}/api/v1/ItemCategory/getById/${id}`);
  }

  addItemCategories(data: any): Observable<any> {
    const ItemCatUrl = `${environment.baseUrlProcurement}/api/v1/ItemCategory/create`;
    return this.http.post<any>(ItemCatUrl, data);
  }

  updateItemCategories(TaxesDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/ItemCategory/update/`;
    return this.http.put<any>(updateBillUrl, TaxesDetails)
  }


  deleteItemCategoriesPemanently(params: any): Observable<any> {
    const ItemCatUrl = `${environment.baseUrlProcurement}/api/v1/ItemCategory/delete`;
    return this.http.delete<any>(ItemCatUrl, {params});
  }


  uploadBatchItemCats(excelUpload: any[]): Observable<any> {
    const ItemCatUrl = `${environment.baseUrlProcurement}/api/v1/ItemCategory/uploadBulk`;
    return this.http.post<any>(ItemCatUrl, excelUpload);
  }

  // ********************************************************************************************************************



}
