import { Component, OnInit } from '@angular/core';


import {
  ApexAxisChartSeries,
  ApexChart,
  ApexXAxis,
  ApexYAxis,
  ApexStroke,
  ApexTooltip,
  ApexDataLabels,
  ApexLegend,
  ApexResponsive,
  ApexPlotOptions,
  ApexFill,
  ApexMarkers,
  ApexGrid,
  ApexTitleSubtitle,
} from 'ng-apexcharts';

import { element } from 'protractor';
import { Observable, Subject, map, takeUntil } from 'rxjs';
import { ItemCategoryService } from 'src/app/erp-inventory/data/services/item-category.service';
import { MainstoreService } from 'src/app/erp-inventory/data/services/mainstore.service';
import { StockManagementService } from 'src/app/erp-inventory/data/services/stock-management.service';
import { StockManagementRoutingModule } from 'src/app/erp-inventory/modules/inventory-management/pages/Inventory-management-routing.module';


export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  xaxis: ApexXAxis;
  yaxis: ApexYAxis;
  stroke: ApexStroke;
  tooltip: ApexTooltip;
  dataLabels: ApexDataLabels;
  legend: ApexLegend;
  responsive: ApexResponsive[];
  plotOptions: ApexPlotOptions;
  fill: ApexFill;
  colors: string[];
  labels: string[];
  markers: ApexMarkers;
  grid: ApexGrid;
  title: ApexTitleSubtitle;
};

@Component({
  selector: 'app-stats',
  templateUrl: './stats.component.html',
  styleUrls: ['./stats.component.sass']
})
export class StatsComponent implements OnInit {


  //variables 
  isLoading: boolean;
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  currentYear = new Date().getFullYear();
  monthsArray: any =
    [
      { name: 'January', value: 1 },
      { name: 'February', value: 2 },
      { name: 'March', value: 3 },
      { name: 'April', value: 4 },
      { name: 'May', value: 5 },
      { name: 'June', value: 6 },
      { name: 'July', value: 7 },
      { name: 'August', value: 8 },
      { name: 'September', value: 9 },
      { name: 'October', value: 10 },
      { name: 'Novembar', value: 11 },
      { name: 'December', value: 12 },
    ]
  currentMonth = this.monthsArray[new Date().getMonth()];
  scales: any[] = [];
  categories: any[] = [];
  // barChartOptions1: any;


  public barChartOptions: Partial<ChartOptions>;
  public lineChartOptions: Partial<ChartOptions>;

  chartDispType: any = ['Year-wise', 'Month-wise'];


  constructor(
    private itemCatMan: ItemCategoryService,
    private stockManService: StockManagementService,
    private mainStoreManService: MainstoreService,
  ) {
    // super();
    this.barChartOptions =
    {
      series: [{
        name: 'category',
        data: this.scales,
      }
      ],
      chart:
      {
        type: 'bar',
        height: 350,
        foreColor: 'green',
        stacked: false,
        toolbar: {
          show: false,
        },
      },
      responsive: [
        {
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0,
            },
          },
        },
      ],
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: '30%',
        },
      },
      xaxis: {
        type: 'category',
        categories: this.categories,
      },
      legend: {
        show: false,
      },
      fill: {
        opacity: 1,
        colors: [
          '#177147', '#397157', '#2D7152', '#22714D'
        ]
      },
      tooltip: {
        marker: {
          show: true,
        },
        x: {
          show: true,
        },
      },
    };
  }

  ngOnInit(): void {

    this.getItemCountPerCategory();
    this.getCategories();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }


  observable = new Observable((observer) => {

    const response = {
      names: [],
      values: [],
    };
    observer.next(response);
  });

  getItemCountPerCategory() {
    this.mainStoreManService
      .getTotalStockPerCategory()
      .pipe(map((response) => {
        const names = response.names;
        const values = response.values;
        return { names, values };
      }))
      .subscribe(
        (res) => {
          this.scales = res.values,
            this.categories = res.names

          console.log(res.names);
          console.log(res.values);



        }
      )
  }

  getCategories() {
    this.itemCatMan.getItemCategories()
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        (res) => {
          if (res.statusCode == 200) {
            if (res.entity.length) {
              // categories = res.entity.categoryName

              res.entity.forEach(element => {
                this.categories.push(element.categoryName)
              });


            } else {
              this.categories = [];

            }
          }
          this.isLoading = false;

        },
        (err) => {
          console.log(err)
        }
      )
  }




}
