import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class FixedAssetsServiceService {

  constructor(
    private http: HttpClient,
  ) { }


 

  getData(): Observable<any[]> {
    const fxAssetUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/getAssetsToUpdate`;
    return this.http.get<any[]>(fxAssetUrl)
  }

  // Update asset manually
  updateAsset(data: any[]): Observable<any> {
    const fxAssetUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/modify`;
    return this.http.put(fxAssetUrl, data);
  }

  modifyAsset(data: any[]): Observable<any> {
    const fxAssetUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/modify`;
    return this.http.put(fxAssetUrl, data);
  }



  

  getAllUpdatedAssets(): Observable<any[]> {
    const fxAssetUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/getUpdatedAssets`;
    return this.http.get<any[]>(fxAssetUrl)
  }

  // add asset manually
  addAsset(data: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/create`;
    return this.http.post<any>(RfqUrl, data);
  }

  // add bulk assets using excel upload
  addBulkAssets(data: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/upload/bulk`;
    return this.http.post<any>(RfqUrl, data);
  }


 

  receiveAssets(): Observable<any> {
    const RfqUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/getAssetsToUpdate`;
    return this.http.get<any[]>(RfqUrl)
  }

  getUpdatedAssets(params: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/getUpdatedAssets`;
    return this.http.get<any[]>(RfqUrl, { params })
  }


   validateAssets(body:any, params: any): Observable<any> {
     const imprestUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/bulkyApproval`;
     return this.http.put<any>(imprestUrl, body, {params:params});
     
   }


   validateasset( body: any, params: any): Observable<any>{
    const receivablePaymentUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/approveOrRejectOrReturn`;
    return this.http.post<any>(receivablePaymentUrl, body,  {params: params} )
  }
  
  
  
  deleteAsset(params: any): Observable<any>   {
    const deleteAssetUrl = `${environment.baseUrlFinance}/api/fixedAssetsMaintenance/delete`;
    return this.http.delete<any[]>(deleteAssetUrl, { params: params, });
  }

  
  
}
